import React from "react";
import * as UI from "./styles";

const Contacts = ({ contacts }) => {
  return (
    <UI.Wrapper>
      {contacts.map(contact => (
        <UI.Link href={contact.link} target="_blank">
          <UI.Icon src={contact.icon} />
        </UI.Link>
      ))}
    </UI.Wrapper>
  );
};

export default Contacts;
